@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  background-image: linear-gradient(to bottom, #946064, #304651);
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

#root {
  margin-top: 10rem;
  margin-bottom: 10rem;
  max-width: 32rem;
  width: 100%;
}